<template>
  <div name="home">
    <image-slider v-if="home.length > 0"
                  :imagePath="imagePath"
                  :images="home"/>
  </div>
</template>

<script>
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import * as homeTypes from 'src/home/store/mutation-types'
  import * as dashTypes from 'src/dash/store/mutation-types'

  export default {
    name: 'home',
    created () {
      this.setDashBodyLoading(true)
      this.receivedHome()
    },
    computed: {
      ...mapGetters({
        imagePath: homeTypes.GET_HOME_IMAGE_PATH,
        home: homeTypes.GET_HOME
      })
    },
    methods: {
      ...mapMutations({
        setDashBodyLoading: dashTypes.SET_DASH_BODY_LOADING
      }),
      ...mapActions({
        receivedHome: homeTypes.RECEIVED_HOME
      })
    }
  }
</script>


