<template>
  <div name="work1">
  </div>
</template>

<script>
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import * as work1Types from 'src/work1/store/mutation-types'
  import * as dashTypes from 'src/dash/store/mutation-types'

  export default {
    name: 'work1',
    created () {
      this.setDashBodyLoading(true)
      this.receivedWork1()
    },
    computed: {
      ...mapGetters({
        work1: work1Types.GET_WORK1
      })
    },
    methods: {
      ...mapMutations({
        setDashBodyLoading: dashTypes.SET_DASH_BODY_LOADING
      }),
      ...mapActions({
        receivedWork1: work1Types.RECEIVED_WORK1
      })
    }
  }
</script>


