import * as work1Types from 'src/work1/store/mutation-types'
import * as dashTypes from 'src/dash/store/mutation-types'
import work1 from 'src/work1/api'
import swal from 'sweetalert'
import store from 'src/store'

export default {
  state: {
    work1: {
      head: '',
      body1: '',
      body2: ''
    }
  },
  getters: {
    [work1Types.GET_WORK1] (state) {
      return state.work1
    }
  },
  mutations: {
    [work1Types.SET_WORK1] (state, work1) {
      state.work1 = work1
    }
  },
  actions: {
    [work1Types.RECEIVED_WORK1] ({ commit }) {
      work1.getWork1()
        .then((response) => {
          const data = JSON.parse(response.request.response)
          commit(work1Types.SET_WORK1, data)
          store.commit(dashTypes.SET_DASH_BODY_LOADING, false)
        })
        .catch((error) => {
          const data = error.response.data
          swal({title: '', text: data.response, html: true, type: 'error'})
        })
    }
  }
}



