<template>
  <div name="personal1">
  </div>
</template>

<script>
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import * as personal1Types from 'src/personal1/store/mutation-types'
  import * as dashTypes from 'src/dash/store/mutation-types'

  export default {
    name: 'personal1',
    created () {
      this.setDashBodyLoading(true)
      this.receivedPersonal1()
    },
    computed: {
      ...mapGetters({
        personal1: personal1Types.GET_PERSONAL1
      })
    },
    methods: {
      ...mapMutations({
        setDashBodyLoading: dashTypes.SET_DASH_BODY_LOADING
      }),
      ...mapActions({
        receivedPersonal1: personal1Types.RECEIVED_PERSONAL1
      })
    }
  }
</script>


