import * as work2Types from 'src/work2/store/mutation-types'
import * as dashTypes from 'src/dash/store/mutation-types'
import work2 from 'src/work2/api'
import swal from 'sweetalert'
import store from 'src/store'

export default {
  state: {
    work2: {
      head: '',
      body1: '',
      body2: ''
    }
  },
  getters: {
    [work2Types.GET_WORK2] (state) {
      return state.work2
    }
  },
  mutations: {
    [work2Types.SET_WORK2] (state, work2) {
      state.work2 = work2
    }
  },
  actions: {
    [work2Types.RECEIVED_WORK2] ({ commit }) {
      work2.getWork2()
        .then((response) => {
          const data = JSON.parse(response.request.response)
          commit(work2Types.SET_WORK2, data)
          store.commit(dashTypes.SET_DASH_BODY_LOADING, false)
        })
        .catch((error) => {
          const data = error.response.data
          swal({title: '', text: data.response, html: true, type: 'error'})
        })
    }
  }
}



