import * as personal1Types from 'src/personal1/store/mutation-types'
import * as dashTypes from 'src/dash/store/mutation-types'
import personal1 from 'src/personal1/api'
import swal from 'sweetalert'
import store from 'src/store'

export default {
  state: {
    personal1: {
      head: '',
      body1: '',
      body2: ''
    }
  },
  getters: {
    [personal1Types.GET_PERSONAL1] (state) {
      return state.personal1
    }
  },
  mutations: {
    [personal1Types.SET_PERSONAL1] (state, personal1) {
      state.personal1 = personal1
    }
  },
  actions: {
    [personal1Types.RECEIVED_PERSONAL1] ({ commit }) {
      personal1.getPersonal1()
        .then((response) => {
          const data = JSON.parse(response.request.response)
          commit(personal1Types.SET_PERSONAL1, data)
          store.commit(dashTypes.SET_DASH_BODY_LOADING, false)
        })
        .catch((error) => {
          const data = error.response.data
          swal({title: '', text: data.response, html: true, type: 'error'})
        })
    }
  }
}



