import * as personal2Types from 'src/personal2/store/mutation-types'
import * as dashTypes from 'src/dash/store/mutation-types'
import personal2 from 'src/personal2/api'
import swal from 'sweetalert'
import store from 'src/store'

export default {
  state: {
    personal2: {
      head: '',
      body1: '',
      body2: ''
    }
  },
  getters: {
    [personal2Types.GET_PERSONAL2] (state) {
      return state.personal2
    }
  },
  mutations: {
    [personal2Types.SET_PERSONAL2] (state, personal2) {
      state.personal2 = personal2
    }
  },
  actions: {
    [personal2Types.RECEIVED_PERSONAL2] ({ commit }) {
      personal2.getPersonal2()
        .then((response) => {
          const data = JSON.parse(response.request.response)
          commit(personal2Types.SET_PERSONAL2, data)
          store.commit(dashTypes.SET_DASH_BODY_LOADING, false)
        })
        .catch((error) => {
          const data = error.response.data
          swal({title: '', text: data.response, html: true, type: 'error'})
        })
    }
  }
}



