import Vue from 'vue'
import Vuex from 'vuex'

import admin from 'src/admin/store/index.js'
import dash from 'src/dash/store/index.js'
import home from 'src/home/store/index.js'
import personal1 from 'src/personal1/store/index.js'
import personal2 from 'src/personal2/store/index.js'
import work1 from 'src/work1/store/index.js'
import work2 from 'src/work2/store/index.js'
import bio from 'src/bio/store/index.js'
import contact from 'src/contact/store/index.js'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    admin,
    dash,
    home,
    personal1,
    personal2,
    work1,
    work2,
    bio,
    contact
  }
})



