<template>
  <div name="work2">
  </div>
</template>

<script>
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import * as work2Types from 'src/work2/store/mutation-types'
  import * as dashTypes from 'src/dash/store/mutation-types'

  export default {
    name: 'work2',
    created () {
      this.setDashBodyLoading(true)
      this.receivedWork2()
    },
    computed: {
      ...mapGetters({
        work2: work2Types.GET_WORK2
      })
    },
    methods: {
      ...mapMutations({
        setDashBodyLoading: dashTypes.SET_DASH_BODY_LOADING
      }),
      ...mapActions({
        receivedWork2: work2Types.RECEIVED_WORK2
      })
    }
  }
</script>


