import { instance, endpoint, isMock, generator } from 'src/api'
import { mocker } from 'src/personal2/api/mocker'

export default {
  getPersonal2 () {
    if (isMock) return generator(mocker.personal2())
    return instance().get(endpoint + '/personal2')
  }
}



