<template>
  <div name="personal2">
  </div>
</template>

<script>
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import * as personal2Types from 'src/personal2/store/mutation-types'
  import * as dashTypes from 'src/dash/store/mutation-types'

  export default {
    name: 'personal2',
    created () {
      this.setDashBodyLoading(true)
      this.receivedPersonal2()
    },
    computed: {
      ...mapGetters({
        personal2: personal2Types.GET_PERSONAL2
      })
    },
    methods: {
      ...mapMutations({
        setDashBodyLoading: dashTypes.SET_DASH_BODY_LOADING
      }),
      ...mapActions({
        receivedPersonal2: personal2Types.RECEIVED_PERSONAL2
      })
    }
  }
</script>


