import { instance, endpoint, isMock, generator } from 'src/api'
import { mocker } from 'src/personal1/api/mocker'

export default {
  getPersonal1 () {
    if (isMock) return generator(mocker.personal1())
    return instance().get(endpoint + '/personal1')
  }
}



